<template>
  <div class="from-warpper">
    <div class="title"></div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm" label-position="left">
      <el-form-item label="id" prop="space_num" >
        <el-input v-model="ruleForm.space_num" placeholder="id"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="ruleForm.status" placeholder="">
          <el-option label="" value="reserved"></el-option>
          <el-option label="" value="unoccupied"></el-option>
          <el-option label="" value="engaged"></el-option>
        </el-select>
      </el-form-item>
      <div class="button">
        <el-button type="primary" @click="submitForm()"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ParkingspaceModification',
  data  () {
    return {
      ruleForm: {
        space_num: '',
        status: ''
      },
      rules: {
        space_num: [
          { required: true, message: 'id', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      let url = '/carport/' + this.ruleForm.space_num + '/'
      console.log(this.ruleForm.space_num)
      console.log(this.ruleForm.status)
      console.log(this.$store.state.id)
      console.log(url)
      var that = this
      axios({
        method: 'patch',
        url: url,
        data: {
          id: that.ruleForm.space_num,
          status: that.ruleForm.status,
          parkinglot: that.$store.state.id
        }
      }).then(function (response) {
        console.log(response)
        console.log('')
      }).catch(function (error) {
        console.log(error)
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.ruleForm.status = ''
    }
  },
  mounted () {
  }
}
</script>

<style lang='stylus' scoped>
  .from-warpper
    width 95%
    padding 20px
    border-radius 10px
    background-color white
    .title
      height 30px
      line-height 30px
      font-size 25px
      margin-bottom 30px
    .button
      text-align center
</style>



