<template>
  <div>
    <div class="from-warpper">
      <div class="title"></div>
      <div id="map-container"></div>
      <div class="viewmode-group">
        <button id="btn3D" class="btn btn-default" @click="click2D"></button>
      </div>
      <div class="parking fix" id="parking"><span id="carid"></span><span id="YorN"></span></div>
      <div class="codition fix">
        <ul>
          <li><span class="codition-first"></span></li>
          <li><span class="codition-second"></span></li>
          <li><span class="codition-third"></span></li>
        </ul>
      </div>
      <div class="i-test-tip fix" id="i-test-tip">
        {{alldata}} {{freedata}}
      </div>
    </div>

  </div>
</template>

<script>
  import $ from 'jquery'
  import axios from 'axios'
  export default {
    name: "gfz",
    data () {
      return {
        alldata:60,
        parkData:"",
        statusname:["","",""],
        pos:0,
        freedata:0
      }
    },
    mounted() {
      window.map = new esmap.ESMap({
        container: document.getElementById("map-container"), //
        //static
        // 
        // 1. 
        // 2.  ID/ 
        //    : ID test666  1004
        //    static/esmap/mapData/test666/test666.esmap
        //       static/esmap/theme/1004/1004.theme
        mapDataSrc: "static/gfz/mapData/", //
        mapThemeSrc: "static/gfz/theme/", //
        themeID: 1005, //
        focusFloor: 1,
        // tokenesmaptokenIDtoken
        token: 'admin123'
      });

      //
      map.openMapById('gfz001');

      //
      // var ctlOpt1 = new esmap.ESControlOptions({
      //   position: 4, // 
      //   //x,y
      //   offset: {
      //     x: 20,
      //     y: 80
      //   }
      //   , imgURL: "static/gfz/resource/style/wedgets/img/"
      // });

      var ctlOpt = new esmap.ESControlOptions({
        position: esmap.ESControlPositon.RIGHT_TOP,
        imgURL: "static/gfz/resource/style/wedgets/"
      });
      //
      map.showCompass = true;

      var that = this
      var pos = 0
      map.on("loadComplete", function () {
        // console.log("")
        // var zoomControl = new esmap.ESZoomControl(map, ctlOpt1);
        // that.marquee();
        setTimeout(function () {that.CallLoadData()},10);
        setInterval(function () {
          that.CallLoadData();
        }, 10000);
        // console.log("")
      })


      map.on("mapClickNode", function (event) {
        if (event.nodeType == esmap.ESNodeType.NONE ||
          event.nodeType == esmap.ESNodeType.FLOOR ||
          event.name == "")
          return;
        $("#parking").css("fontSize", "18px").html();
        $("#carid").css("color", "rgb(255, 255, 0)").html(event.name); //ID
        for (var i = 0; i < that.$data.parkData.length; ++i) {
          if (event.name == that.$data.parkData[i].code) {
            console.log(that.$data.parkData[i].condition)
            $("#YorN").html(that.$data.parkData[i].condition);
          }
        }
      });
    },
    methods: {
      click2D () {
        //3D2D
        if( map.viewMode === esmap.ESViewMode.MODE_2D){
          map.viewMode = esmap.ESViewMode.MODE_3D
        }
        if (map.viewMode === esmap.ESViewMode.MODE_3D) {
          map.viewMode = esmap.ESViewMode.MODE_2D
        }
      },
      CallLoadData () {
        var  color = ["#FFFF00", "#ff0000", "#00ff00"]
        var id =[]
        var that = this
        // console.log(that.$data.parkData)
        axios({
          method: 'get',
          url: '/carport/',
          data: {
            parkinglot: that.$store.state.id,
          },
          params: {
            search:"A",
            page_size:60
          }
        }).then(function (res) {
          //map
          console.log("")
          console.log(res.data)
          //parkData
          // console.log(res.data.put)
          that.$data.parkData = res.data.results
          // console.log("")
          var showtext= "";
          var freecars = 0
          //2.
          //mydatamydata.size1
          for(var i =0;i<that.$data.parkData.length;i++){
            var temp = that.changeColor(that.$data.parkData[i].status)
            console.log(temp)
            window.map.changeModelColor({
              name:that.$data.parkData[i].code,
              color: temp
            });
            if( temp == "#99CCFF" ){
              freecars = freecars+1
            }
            that.$data.freedata = freecars
          }
        })
      },
      changeColor (status) {
        if (status == "unoccupied")
          return "#99CCFF"
        if (status == "engaged")
          return '#ff0000'
        if (status == "reserved")
          return '#00FF00'
      },
      // marquee() {
      //   var scrollWidth = $('#i-test-tip').width();
      //   var textWidth = $('.test-tip').width();
      //   var i = scrollWidth;
      //   setInterval(function () {
      //     i--;
      //     if (i < -textWidth) {
      //       i = scrollWidth;
      //     }
      //     $('.test-tip').animate({
      //       'left': i + 'px'
      //     }, 8);
      //   }, 8);
      // }
    }
  }
</script>

<style scoped lang='stylus'>
  .viewmode-group {
    position: absolute;
    right: 100px;
    top: 32%;
    border-radius: 6px;
    border: none;
  }

  .viewmode-group button {
    display: inline;
    width: 38px;
    height: 38px;
    border-radius: 4px;
    border: none;
    background-image: url("/static/esmap/resource/style/wedgets/img/3D.png")
    background-repeat: no-repeat
  }

  .parking {
    width: 320px;
    height: 46px;
    line-height: 46px;
    left: 500px;
    bottom: 14px;
    border: 1px solid #083344;
    border-radius: 4px;
    color: rgb(255, 255, 255);
    background-color: rgba(71, 92, 105, 0.8);
    font-size: 16px;
    text-align: center;
  }

  .fix {
    position: fixed;
  }

  .codition {
    width: 120px;
    left: 300px;
    bottom: 120px;
  }

  .codition ul {
    width: 100%;
    padding: 6px;
    list-style-type: none;
  }

  .codition ul li {
    display: list-item;
    height: 36px;
    background-color: rgb(255, 255, 255);
    line-height: 36px;
    text-align: center;
  }

  .codition ul li span {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    margin-right: 10px;
    vertical-align: middle;
  }

  .codition-first {
    background-color: #f00;
  }

  .codition-second {
    background-color: #99CCFF
  }

  .codition-third {
    background-color: #00FF00;
  }

  .i-test-tip {
    width: 400px;
    height: 46px;
    line-height: 46px;
    left:1000px;
    bottom: 14px;
    border: 1px solid #083344;
    border-radius: 4px;
    color: rgb(255, 255, 255);
    background-color: rgba(71, 92, 105, 0.8);
    font-size: 16px;
    text-align: center;
  }

  .test-tip span {
    color: #0f0;
  }
  #map-container{
    height: 510px;
    width: 100%;
  }
  .from-warpper
    width 95%
    padding 20px
    border-radius 10px
    background-color white
    .title
      height 30px
      line-height 30px
      font-size 25px
      margin-bottom 30px
</style>



